h1 {
  text-align: center;
}
.grid {
  max-width: 1100px;
  margin: 5%;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(300px, auto));
  justify-items: center;
}

.card {
  min-height: 400px;
  height: min;
  min-width: 300px;
  width: 100%;
  background-image: linear-gradient(30deg, #211010, #5e2222);
  box-shadow: 0px 3px 10px #fff1;
  border: 1px solid #444;
  border-radius: 0.5rem;
  overflow: hidden;
  cursor: pointer;
  scroll-snap-align: start;
}

.image {
  height: 200px;
  width: 100%;
  background-position: 30% 30%;
  background-size: cover;
  background-repeat: no-repeat;
  filter: grayscale(100%);
}

.card h2 {
  margin: 0.5rem 0.5rem;
  text-align: left;
}

.tags {
  display: flex;
  margin: 0;
}

.tag {
  margin: 0.2rem 0.5rem;
  padding: 0.2rem 0.5rem;
  background-color: skyblue;
  color: #333;
  border-radius: 1rem;
}

.description {
  margin: 1rem;
}
