a,a:visited {
  text-decoration: none;
  color: inherit;
}

body {
  background-color: #000;
}

.container {
  min-height: 100vh;
  width: 100%;
  margin: auto;
  background-image: url("../../public/bg.png");
  background-size: cover;
  color: #aaa;
}

.nav {
  height: 50px;
  width: 90%;
  max-width: 1100px;
  margin: 0.5rem auto;
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #333;
  box-shadow: 0px 3px 20px rgba(255,255,255,0.1);
  z-index: 6;
}

.nav ul {
  display: flex;
  padding:0;
  justify-content: flex-start;
}

.nav li {
  list-style: none;
  margin: 0.5rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.nav li:first-child {
  margin-left: 1.5rem;
}

.nav li:hover {
  color: dodgerblue;
  box-shadow: 0 0 0 1px dodgerblue;
}

.controls {
  margin: 0;
  height: 80%;
  width: 3rem;
  /* overflow: hidden; */
  border-radius: 10px;
}


.footer {
  font-size: 0.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem auto 0.1rem auto;
  padding: 0.3rem;
  background-image: linear-gradient(30deg, #211010, #5e2222);
  border-radius: 0.3rem;
}

