h1,h2 {
  color: dodgerblue;
}

.main {
  max-width: 1100px;
  margin: 5%;
  z-index: 5;
}

.tech {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(auto-fill, minmax(120px, auto)) ;
}

.card{
  border: 1px solid white;
  position: relative;
  width: 100%;
  display: flex;
  border: 1px solid #55555599;
  border-radius: 1rem;
  background-color: #333;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.05);
  z-index: 1;
}

.card:nth-child(1) {
  background-color: #f7df1e;
}

.card:nth-child(3) {
  background-color: #007acc;
}

.card:hover .toolname,
.card:active .toolname {
  left: 0;
}

img {
  width: 100%;
}

.toolname {
  padding: 0;
  margin: 0;
  transition: 0.2s;
  color: dodgerblue;
  font-size: 1.4rem;
  font-weight: bold;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  position: absolute;
  top: 0;
  left: -110%;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 980px) {
  .main {
    margin: 2rem;
  }

  .tech {
    grid-gap: 1.5rem;
  }

  .card {
    border-radius: 0.8rem;
  }

  .toolname {
    font-size: 1.2rem;
  }
  .toolname:nth-child(1),
  .toolname:nth-child(2) {
    font-size: 1.2rem;
  }
}

@media (max-width: 768px) {
  .main {
    margin: 2rem;
  }

  .tech {
    grid-gap: 1.5rem;
  }

  .card {
    border-radius: 0.8rem;
  }

  .toolname {
    font-size: 1.2rem;
  }
}

@media (max-width: 500px) {
  .main {
    margin: 1rem;
  }
  .tech {
    grid-gap: 0.8rem;
  }
  .card {
    border-radius: 0.5rem;
  }
  .toolname {
    font-size: 1rem;
  }
}
